export const getTimespanDays = (days: number) => {
    if (days === 7) return '';
    if (days === 30) return '';
    if (days === 365) return '';
    if (days > 10 && [11, 12, 13, 14].includes(days % 100)) return `${days} `;
    const last_num = days % 10;
    if (last_num === 1) return `${days} `;
    if ([2, 3, 4].includes(last_num)) return `${days} `;
    if ([5, 6, 7, 8, 9, 0].includes(last_num)) return`${days} `;
};
